import React, { useState, useContext } from 'react';
import { ExplorerContext } from '../../context/ExplorerContext';
import { formatEther, isAddress } from 'ethers';
import styles from './AddressLookup.module.css';

const AddressLookup: React.FC = () => {
  const context = useContext(ExplorerContext);
  if (!context) throw new Error('ExplorerContext not found');
  const { provider } = context;
  const [address, setAddress] = useState('');
  const [balance, setBalance] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleLookup = async () => {
    setError(null);
    setBalance(null);
    if (!isAddress(address)) {
      setError('   .');
      return;
    }
    try {
      const bal = await provider.getBalance(address);
      setBalance(formatEther(bal));
    } catch (e: any) {
      console.error(e);
      setError('    .');
    }
  };

  return (
    <div className={styles.container}>
      <h3 className={styles.title}>Address Lookup</h3>
      <input
        type="text"
        placeholder="  "
        value={address}
        onChange={(e) => setAddress(e.target.value)}
        className={styles.input}
      />
      <button onClick={handleLookup} className={styles.button}></button>
      {error && <div className={styles.error}>{error}</div>}
      {balance && (
        <div className={styles.result}>
          <div>Address: {address}</div>
          <div>Balance: {balance} ETH</div>
        </div>
      )}
    </div>
  );
};

export default AddressLookup;
